import RandomCatchItem from "./RandomCatchItem";
import useFetch from "./useFetch";

const RandomCatch = () => {

    const { data, isPending, error } = useFetch('https://www.fishwatch.gov/api/species');

    function checkCatch(randomCatch, newCatch, catchLimit) {

        while (randomCatch.includes(newCatch)) {

            newCatch += 1;

            if (newCatch === catchLimit) {
                newCatch = 0;
            };
        };

        return newCatch;
    }

    function generateRandomCatchId() {
        const catchLimit = 100
        let randomCatch = [];

        for (let i = 0; i < 8; i++) {
            let newCatch = Math.floor(Math.random() * catchLimit);
            newCatch = checkCatch(randomCatch, newCatch, catchLimit);
            randomCatch.push(newCatch);
        };

        //return randomCatch;
    }

    return (
        <div className='random-catch flex'>
            <h4>Random Catch</h4>
            <div className='catch-item-container flex'>
                <RandomCatchItem randomCatch={ generateRandomCatchId() } />
            </div>
            <button>Randomize</button>
        </div>
    );
}
 
export default RandomCatch;